import React from "react";
import "./Navbar.css";
import { Link, useLocation } from "react-router-dom";
import { IconRoutesPages } from "./IconRoutesPages";

const Navbar = ({ isMobile, height = "3rem" }) => {
  const { pathname } = useLocation();
  return (
    <nav
      className={`styleNavbar d-flex align-items-end justify-content-center ps-1 ps-md-auto text-white w-100 ${
        isMobile && "position-fixed bottom-0"
      }`}
      style={{
        height,
        backgroundColor: "var(--primary)",
        overflowX: "auto",
        overflowY: "hidden",
      }}
    >
      <ul className="d-flex w-100 justify-content-around  align-items-center mb-0 ps-0 w-100 fs-3 ">
        {IconRoutesPages.map(({ icon, to }) => (
          <Link
            to={to}
            style={
              isMobile && {
                padding: "5px 7px",
                borderRadius: "0px 0px 10px 10px",
                transform: "translateY(0rem)",
                transition: "0.5s",
              }
            }
            className={`linkNavbar ${
              pathname === to &&
              (isMobile ? " activeIconMobile " : " activeIcon ")
            } `}
          >
            {icon}
          </Link>
        ))}
      </ul>
    </nav>
  );
};
export default Navbar;
